body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #160A22;
  padding: 0px 10px;
  color: #888888;
}

ul {
  text-align: left;
}

kbd {
    border-radius: 8px;
    padding: 3px 6px;
    background-color: #888888;
    color: #160A22;
    border: 1px solid black;
    font-size: 0.6em;
    position: relative;
    bottom: 3px;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2vw);
}
.App > h3 {
  font-size: calc(3vw);
}
.App > h2 {
  font-size: calc(4vw);
}

/* less than 7 */
.big-cells .gridrow {
  height: calc(4vw);
}
.big-cells .cell {
  height: calc(4vw);
  width: calc(4vw);
  line-height: calc(4vw);
}

/* less than 11 */
.medium-cells .gridrow {
  height: calc(3vw);
}
.medium-cells .cell {
  height: calc(3vw);
  width: calc(3vw);
  line-height: calc(3vw);
}

.gridrow {
  margin: 0px;
  height: calc(2vw);
}

.cell {
  height: calc(2vw);
  width: calc(2vw);
  line-height: calc(2vw);
  display: inline-block;
  position: relative;
}
.cell > div {
  position: absolute;
}

.player {
  background-size: 100%;
  height: 100%;
  width: 100%;
  margin: 0%;
}

.wall {
  background-color: #888888;
}
.rock {
  background-color: #884444;
  border-radius: 50%;
  width: 68%;
  height: 68%;
  margin: 16%;
}
.exit {
  /* background-color: #77aabb; */
  background-color: transparent;
}
.wormholeIn, .exit {
    box-sizing: border-box;
    border-radius: 50%;
    border-style: solid;
    border-width: 0.1em;
}
.big-cells .cell > div > .text {
  font-size: calc(1.5vw)
}
.cell > div > .text {
  color: #160A22;
  font-size: calc(0.7vw);
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 2px;
  height: 100%;
}
.wormholeOut, .enter {
    border-radius: 50%;
    border-style: solid;
    border-width: 0.1em;
    box-sizing: border-box;
    background-color: 'transparent';
}
.enter {
  color: rgba(0, 0, 0, 0) /* transparent */
}

.shiny {
    animation: spinCoin infinite 1s linear;
    background-color: #fff3ce;
    border-radius: 50%;
    width: 40%;
    height: 60%;
    margin: 20% 30% 20% 30%;
}
@keyframes spinCoin {
  from {
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
    -transform: rotateY(0deg);
  } to {
    -webkit-transform: rotateY(360deg);
    -moz-transform: rotateY(360deg);
    -o-transform: rotateY(360deg);
    -transform: rotateY(360deg);
  }
}

.twig {
  background:
         linear-gradient(to top left,
             rgba(0,0,0,0) 0%,
             rgba(0,0,0,0) calc(50% - 0.8px),
             rgba(0,0,0,1) 50%,
             rgba(0,0,0,0) calc(50% + 0.8px),
             rgba(0,0,0,0) 100%),
         linear-gradient(to top right,
             rgba(0,0,0,0) 0%,
             rgba(0,0,0,0) calc(50% - 0.8px),
             rgba(0,0,0,1) 50%,
             rgba(0,0,0,0) calc(50% + 0.8px),
             rgba(0,0,0,0) 100%);
}

p {
  font-size: 12px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
